















































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			static:'',
			swipeList: ['../../static/index/banner.jpg','../../static/index/banner.jpg'],
			indicatorDots: true,
			indicatorcolor: 'white',
			indicatoractivecolor:'#FEC200',
			autoplay: true,
			interval: 5000,
			duration: 1000,
			circular: true,
			newlist: [],
			recomlist: [],
			classlist:[],
			provinceName:'',
			countyAreaName:'',
			cityName:''
		}
	},
	onLoad() {
		this.static=ut.static;
	},
	onShow() {
		if(!this.newlist.length){
			this.req_new();
		}
		if(!this.recomlist.length){
			this.req_recom();
		}
		if(!this.classlist.length){
			this.req_class();
		}
	},
	methods: {
		bindRegionChange: function (e) {
			this.provinceName=e.target.value[0];
			this.countyAreaName=e.target.value[1];
			this.cityName=e.target.value[2];
		},
		go_home_serch(){
			wx.navigateTo({
				url: '../home/serch'
			})
		},
		go_home_server(_id,name){
			console.log(_id)
			wx.navigateTo({
				url: `../home/server?_id=${_id}&title=${name}`
			})
		},
		go_home_serverinf(_id,name){
			wx.navigateTo({
				url: `../home/serverinf?_id=${_id}&title=${name}`
			})
		},
		go_home_reform(){
			wx.navigateTo({
				url: '../home/reform'
			})
		},
		go_home_reform1(){
			wx.navigateTo({
				url: '../home/reform1'
			})
		},
		call(){
			ut.call();
		},
		req_class(){
			ut.request({
				data: {
					parentid:0
				},
				url: "service/class"
			}).then(data=>{
				this.classlist=data;
			})
		},
		req_new(){
			ut.request({
				data: {
					parentid:0
				},
				url: "service/newlist"
			}).then(data=>{
				this.newlist=data
			})
		},
		req_recom(){
			ut.request({
				data: {
					parentid:0
				},
				url: "service/recommendlist"
			}).then(data=>{
				this.recomlist=data
			})
		},
		//打开授权
		opensetting(){
			this.wxgetlocation()
		},
		wxgetlocation() {
			wx.authorize({
			scope: "scope.userLocation",
			success:function(){
				wx.getLocation({
					type: 'wgs84',
					success: function(res) {
						console.log(res)
						const latitude = res.latitude;
						const longitude = res.longitude;
						const speed = res.speed;
						const accuracy = res.accuracy;
					}
				})
			},
			fail(){
				ut.totast('获取地址失败');
			}
		})
		}
	}
}
